.greetingForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
}

.inputAndButtonContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.error {
    color: red;
    font-size: 12px;
    margin-top: 5px;
}

.input {
    padding: 5px;
    border: 1px solid green;
    border-radius: 4px;
    margin-right: 10px;
}

.input:focus {
}

.errorInput {
    padding: 5px;
    border: 1px solid red;
    border-radius: 4px;
    margin-right: 10px;
}

.button {
    padding: 5px 10px;
    background-color: blue;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    background-color: gray;
    cursor: not-allowed;
}

.text {
}

.greeting {
    margin-top: 10px;
    font-size: 18px;
    font-weight: bold;
}
